<template>
  <div id="register">
    <el-container style="height: 868px">
      <el-header class="first-header" style="display: flex">
        <img src="../../static/logo.png" style="width: 13%;position: relative;left: 10%" >
      </el-header>
      <el-main  style="position: relative;height: 100%">
        <div class="register-container" style="display: flex;height: 100%">
          <div class="register" style="position: relative;width: 70%;left: 15%;height: 100%">
            <div class="step" >
              <el-steps :active="active" finish-status="success" align-center="true">
                <el-step title="验证邮箱"></el-step>
                <el-step title="填写账号"></el-step>
                <el-step title="注册成功"></el-step>
              </el-steps>
            </div>
            <div style="position: relative;top: 10%">
              <div class="grid-content bg-purple-light" >
                <div class="active1" style="display: inline;position: relative;width: 70%;left: 15%">
                  <div class="email" v-if="active == 1" style="width:50%; display: flex;">
                    <el-form  ref="form"  label-width="60px" :rules="rules" :model="registerform">
                      <el-form-item label="邮箱"  prop="email">
                        <el-input v-model="registerform.email" ></el-input>
                      </el-form-item>
                    </el-form>
                  </div>
                  <div class="codecontent" v-if="active == 1" style="width:10%; display: flex;">
                    <el-form ref="form" :model="registerform" label-width="60px">
                      <el-form-item label="验证码" prop="testcode">
                        <el-input v-model="registerform.testcode" ></el-input>
                        <el-button v-show="show" @click="getCode">获取验证码</el-button>
                        <el-button v-show="!show" class="count">{{ count }} s</el-button>
                      </el-form-item>
                    </el-form>
                  </div>
                </div>
                <div class="active2" style="display: inline;position: relative;width: 30%;left: 15%">
                  <div class="pwdfirst" v-if="active == 2" style="width: 30%">
                    <el-form ref="form" :model="registerform" label-width="80px" >
                      <el-form-item label="密码" prop="pwdfirst">
                        <el-input v-model="registerform.pwdfirst" show-password></el-input>
                      </el-form-item>
                    </el-form>
                  </div>
                  <div class="pwdagain" v-if="active == 2" style="width: 30%">
                    <el-form ref="form" :model="registerform" label-width="80px" :rules="rules">
                      <el-form-item label="确认密码" prop="pwdagain" >
                        <el-input v-model="registerform.pwdagain" show-password></el-input>
                      </el-form-item>
                    </el-form>
                  </div>
                </div>
                <div class="registersuccess" v-if="active == 3" style="display: inline;position: relative;height: 80%">
                  <el-button type="success" plain  @click="returntomain"  style="left: 30%;position: relative">返回主界面</el-button>
                  <div>
                    <span style="left: 30%;position: center;font-size: 50px">注册成功！</span>
                  </div>
                </div>
              </div>
            </div>
            <div class="btn" style="position: relative;top:20%">
              <!-- <button>下一步</button> -->
              <el-button type="danger" @click="verification" v-if="active==1 || active==2">下一步</el-button>
            </div>
          </div><!-- 底部 -->
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import router from "@/router";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Register",
  data() {
    var checkemail = (rule, value, callback) => {
      console.log(value)
      console.log(this.registerform.testcode)
      if (!/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)) {
        callback(new Error('请输入正确的邮箱地址'));
      }
      else{
        callback();
      }
    };

    var checkpwd = (rule, value, callback) => {
      console.log(this.registerform.pwdfirst)
      console.log(this.registerform.pwdagain)
      if(this.registerform.pwdfirst!=value)
      {
        callback(new Error('请确保输入的密码一致'));
      }
      else
      {
        callback();
      }
    };


    return {
      show: true,
      count: "",
      timer: null,
      active: 3,
      input: "",
      testcode1:'',
      email1:'',
      registerform: {
        email:'',
        pwdfirst:'',
        pwdagain:'',
        testcode:'',
        testcodeuser:'@/',
      },

      rules: {
        email:[
          {validator:checkemail,trigger:'blur'}
        ],
        pwdagain:[
          {validator:checkpwd,trigger:'blur'}
        ]
      }
    };
  },
  methods: {
    getCode() {
      const TIME_COUNT = 10;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }

      axios.post('back/user/register-first',"email="+this.registerform.email).then(res =>
      {
        var result='';
        result=res.data.result
        if(result=='用户已存在'){
          alert('用户已存在，请重新输入邮箱！')
          this.registerform.testcode=''
        }
        else {
          console.log(result)
          this.registerform.testcodeuser = result
        }
      })
    },

    verification(registerform){
      registerform=this.registerform
      if(this.active===1) {
        if (this.registerform.testcode === this.registerform.testcodeuser) {
          this.active++;
        }
        else {
          alert('验证码输入错误！')
        }
      }
      else if(this.active===2)
      {
        if(this.registerform.pwdagain == this.registerform.pwdfirst) {
          axios.post('back/user/register-second', "email=" + this.registerform.email + "&upassword=" + this.registerform.pwdagain).then(res2 => {
            var result2 = '';
            result2 = res2.data.result;
            if (result2 == 'success') {
              alert('注册成功！');
              this.active++;
            } else {
              alert('添加用户失败');
            }
          })
        }
        else{
          alert('两次密码不一致')
        }
      }
    },

    returntomain(){
      router.push('/main')
    }
  },
};
</script>

<style scoped>

</style>
